.app-wrapper {
  display: flex;
}

.app-wrapper-block {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app-wrapper-content {
  width: 60%;
  margin: 0 auto;
}

.login-container {
  margin: auto;
}

.wrapperDirection {
  flex-direction: column-reverse;
  height: 100%;
}

.wrapperBlockBackground {
  background: var(--dark-bg-color);
}

.content {
  display: flex;
  flex-direction: row;
}

.contentMargin {
  margin: auto;
}

@media (max-width: 576px) {
  .app-wrapper-content {
    width: 75%;
  }
}
