.login {
  display: flex;
  margin: 0 auto;
}

.loginSidebar,
.loginBlock {
  width: 50%;
  padding: 5rem 3rem;
  color: var(--section-bg-color);
}

.loginForm {
  width: 100%;
  height: 16rem;
}

.loginSidebar {
  background: var(--accent-color);
  color: var(--main-bg-color);
}

.loginSidebarTitle {
  font-size: 1.616em;
  font-weight: 700;
}

.loginSidebarText {
  font-size: 15px;
  line-height: 2rem;
  padding-bottom: 1.5rem;
}

.loginSidebarIcon {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 1.5rem;
}

.iconBlock {
  border: 2px solid var(--main-bg-color);
  border-radius: 0.5rem;
  height: 2.5rem;
  width: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginLogo {
  text-align: center;
}

.loginLogo img {
  width: 4rem;
}

.loginSidebar p,
.loginSidebar h3 {
  margin: 0;
}

.remember {
  display: flex;
  font-weight: normal;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.6rem;
}

.remember label {
  display: flex;
  margin-left: 0.05rem;
}

.remember span {
  font-size: 0.85rem;
}

.loginBlock {
  background: var(--main-bg-color);
  color: var(--icon-color);
  text-align: center;
  font-size: 14px;
}

.loginBlock .loginSidebarTitle {
  color: var(--dark-bg-color);
}

button[type="submit"] {
  width: 100%;
}

.errorBlock {
  height: 20px;
}

.error {
  font-size: 0.9em;
  color: var(--error-color);
}

.captcha {
  width: 100%;
  min-height: 90px;
  height: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.captcha img {
  width: 10rem;
  margin: 0 auto;
}

.captcha .inputBlock {
  margin: 0;
  padding: 0;
}

.captcha .inputBlock input {
  height: 2rem;
}

.loginButton {
  height: 3rem;
  border: none;
  border-radius: 2rem;
  background-color: #8224e3;
  background-image: linear-gradient(210deg, #a968ec 0%, #8224e3 50%, #8224e3 25%);
  color: #fff;
  cursor: pointer;
}

.testAccount {
  font-size: 10px;
  text-align: center;
  color: var(--mute-text-color);
  height: 1rem;
  border-radius: 1rem;
  background-color: #000000;
  width: fit-content;
  padding: 0.3rem 0.7rem;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99999;
}

@media (max-width: 992px) and (min-width: 768px) {
  .login {
    width: 920px;
    height: 600px;
  }
}

@media (max-width: 768px) and (min-width: 576px) {
  .login {
    width: 750px;
    height: 600px;
  }
}

@media (max-width: 576px) {
  .loginSidebar {
    display: none;
  }

  .loginBlock {
    width: 100%;
  }
}
