.paginatorContainer {
  margin: 1.5rem 0;
}

.paginatorContainer nav {
  margin: 0 auto;
}

.paginatorContainer nav ul {
  font-size: 12px;
  text-align: center;
}

.paginatorContainer nav ul li button {
  color: var(--icon-color);
}

.paginatorContainer nav ul li button:hover {
  color: var(--accent-color);
  background-color: var(--element-bg-color);
}

